.project {
    padding: 15px;
    display: grid;
    width: 90%;
    margin: 5vw 0 2vw 5vw
}

.project>h2 {
    font-size: 45px;
    font-weight: 400;
    text-align: center;
    text-decoration: underline;
    text-decoration-style: solid;
    margin-bottom: 7vh;
    margin-left: -5vw;
}

.childProject {
    display: flex;
    gap: 10px;
    width: 85%;
    justify-content: space-evenly;
    margin-left: 7vw;
}

.childProject>div {
    border: 2px solid white;
    width: 30%;
    height: 540px;
    background-color: #457F89;
    box-shadow: rgba(18, 36, 67, 0.6) 0px 3px 8px;
    padding-bottom: 5px;
}

.childProject>div>img {
    width: 100%;
    height: 50%;
}

.childProject>div>div>:first-child {
    text-align: center;
    padding: 10px;
    line-height: 20px;
}

.childProject>div>div>:first-child>h2 {
    font-size: 25px;
    font-weight: 500;
    text-decoration: underline;

}

.childProject>div>div>:first-child>p {
    font-size: 18px;
    margin-top: -10px;
}

.button1 {
    display: flex;
    gap: 10px;
    justify-content: space-evenly;
    margin-top: 20px;

}

.button1>div {
    width: 35%;
    height: 50px;
    display: grid;
    justify-content: center;
    align-items: center;
    background-color: #2874F0;
    color: yellow;
    border-radius: 15%;
}

.button1>:first-child>img {
    width: 40%;
    margin-left: 25%;
}

.button1>:first-child+div>img {
    width: 20%;
    height: 40%;

}

.button1>:first-child+div {
    display: flex;
}

.button {
    display: flex;
    gap: 10px;
    justify-content: space-evenly;

}

.button>div {
    width: 35%;
    height: 50px;
    display: grid;
    justify-content: center;
    align-items: center;
    background-color: #2874F0;
    color: yellow;
    border-radius: 15%;
}

.button>:first-child>img {
    width: 40%;
    margin-left: 25%;
}

.button>:first-child+div>img {
    width: 20%;
    height: 40%;

}

.button>:first-child+div {
    display: flex;
}

/* media query */

/* tablet view */
@media screen and (min-width:633px) and (max-width:768.5px) {
    .childProject {
        border: 1px solid white;
        flex-direction: column;
        gap: none;
        row-gap: 30px;
        width: 85%;
        margin-left: 5vw;
       }

       .childProject>div{
        margin-left: 25%;
        width: 300px;
        height: 600px;
       }
}
@media screen and (min-width:598px) and (max-width:632.9px) {
      .childProject {
        border: 1px solid white;
        flex-direction: column;
        gap: none;
        row-gap: 30px;
        width: 85%;
        margin-left: 5vw;
       }

       .childProject>div{
        margin-left: 20%;
        width: 300px;
        height: 600px;
       }
}
@media screen and (min-width:426.95px) and (max-width:597.95px) {
    .childProject {
      border: 1px solid white;
      flex-direction: column;
      gap: none;
      row-gap: 30px;
      width: 85%;
      margin-left: 5vw;
     }

     .childProject>div{
      margin-left: 7%;
      width: 300px;
      height: 670px;
     }
}